@import url('https://fonts.googleapis.com/css2?family=Alumni+Sans+Collegiate+One&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Alumni+Sans+Collegiate+One&family=Oswald:wght@200;300;400;500;600;700&display=swap');
*{
    margin: 0;
    padding: 0;
}
.top-navbar{
    position: fixed;
    top: 0;
    width: 100%;
    background: #2f4e93;
    text-align: center;
}
.me{
    display: flex;
    align-items: center;
    padding:9px 20px 9px 20px;
    justify-content: space-between;

}

.trophy{
    display: flex;
    align-items: center;
    gap:5px;
  
}
.menu{
    color: green;
    font-size: 30px;
}
.top-title{
   
    color: white;
    /* padding: 20px 5px 20px 20px; */
    font-family: Oswald;
    text-transform:uppercase;
    letter-spacing: 2px;
    font-weight: 800;
    font-size: 19px;
}

.box{
    width: 100px;
    height: 100px;
    box-shadow: 0px 0px 9px black;
}
.under{
    text-decoration: none;
    cursor: pointer;

}





/*footer */
.bottom{
    display: flex;
    justify-items: center;
    justify-content: space-around;
}
.icon-nav{
    display: flex;
    justify-items: center;
    justify-content: space-around;

}
.homes:hover{
    color: red;

}
.footer{
    background: whitesmoke;
    color: white;
    position: fixed;
    bottom: 0;
    width: 100%;
    padding: 10px 10px 10px 10px;
    box-shadow: 0px 0px 9px rgb(94, 88, 88);
}
.home{
    font-size:14px;
    text-decoration: none;
    font-weight: 500;
}
.home:hover{
    color: gray;
}
.fit{
    display: grid;
    text-align: center;
}
.team{
    border: none;
    outline: none;
    color: black;
    background-color: white;
}
.jointeam{
    display: flex;
    align-items: center;
    background-color: white;
    padding: 2px 5px 2px 5px;
    cursor: pointer;
    gap: 4px;
    border-radius: 5px;
}
.teams{
    display: flex;
    margin: 10px auto;
    border: none;
    outline: none;
    padding:  15px 75px 15px 75px;
    border-radius: 5px;
    color: white;
    font-size: 15px;
    background:linear-gradient(to right, #2f4e93, #355fb9);
}
.scribe{
    color: whitesmoke;
    display: block;
    margin: 10px auto;
    border: none;
    outline: none;
    padding:  5px 30px 5px 30px;
    border-radius: 5px;
    background: linear-gradient(to left,rgb(32, 32, 51),rgb(8, 8, 41));
}
